@import "../../SCSS/generic/reset";
@import "../../SCSS/elements/colors";

.search_form{
 width: 100%;
  height: 31.25rem;
  display: flex;
  justify-content:space-between;
  align-items: center;
text-align: center;
padding: 1rem;

}
label{
  color: ghostwhite;
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 3.125rem;
 margin-top: 2rem;

}
.input_style{
  width: 13rem;
  border: none;
  height: 2.5rem;
  font-size:1.2rem ;
  background-color:map_get($colors,"blue");
  color: ghostwhite;
  font-weight: bold;
  border-radius: 5px;
  box-sizing: border-box;
  padding: 1.6rem;

}

.submit{
  width: 13rem;
  border: none;
  height: 3.2rem;
  font-size:1.2rem ;
  background-color:map_get($colors,"violet");
  color: ghostwhite;
  font-weight: bold;
  border-radius: 5px;
  box-sizing: border-box;
  margin-top:3.125rem;


}